import {Component, EventEmitter, Input, Output, TemplateRef} from '@angular/core';
import {Step} from './step.model';

@Component({
  selector: 'stepper',
  templateUrl: './stepper.component.html',
  styleUrls: ['./stepper.component.scss']
})
export class StepperComponent {

  @Input()
  stepTitleTemplateRef: TemplateRef<void> = null;

  @Input()
  steps: Step[] = [];

  @Input()
  dotSize = 12;

  @Input()
  lineWidth = 3;

  @Input()
  defaultDotColor = '#dddddd';

  @Input()
  defaultDashColor = '#dddddd';

  @Output()
  onDotClick: EventEmitter<Step> = new EventEmitter();


  docClicked(step) {
    this.onDotClick.emit(step);
  }

  dotWidthAndHeight() {
    return this.dotSize + 'px';
  }

  dashTopPosition() {
    return ((this.dotSize + this.lineWidth) / 2) + 'px';
  }

  lineWidthInPixels() {
    return this.lineWidth + 'px';
  }
}
